<style lang="stylus">
  @import 'todo-list'
</style>

<template>
  <div>
    <div class="weui-cell weui-cell_vcode">
      <div class="weui-cell__bd">
        <input id="newContent"
               class="weui-input"
               type="text"
               placeholder="填写记录">
      </div>
      <div class="weui-cell__ft">
        <button @click="addToContent"
                class="weui-vcode-btn">add</button>
      </div>
    </div>
  
    <div v-show="isInEdit"
         @click="clickToSee"
         class="mask"
         :class="isInEdit?'in-edit':''"></div>
    <div class="todo-form">
      <transition-group name="changing"
                        tag="div">
        <div v-for="item in todoList"
             class="todo-menu-item"
             :key="item"
             :class="[{done: item.checked}, {editable: item.editable}]"
             @click="clickToSee"
             @touchmove="touchmove"
             @touchstart="touchstart"
             @touchend="touchend">
          <div class="item-check item-side">√</div>
          <div class="item-content">
            <span class="content-show"
                  v-show="!item.editable"
                  @click="clickToEdit">{{item.content}}</span>
            <input class="content-input"
                   type="text"
                   @click="clickToStop"
                   v-show="item.editable"
                   v-model="item.content" />
          </div>
          <div class="item-delete item-side">X</div>
        </div>
      </transition-group>
    </div>
  </div>
</template>
<script>
import 'weui'
export default {
  data() {
    return {
      todoList: localStorage.getItem('datas') ? JSON.parse(localStorage.getItem('datas')) : [
        {
          content: '上方添加',
          checked: false,
          editable: false
        },
        {
          content: '未完成右滑动变已完成',
          checked: false,
          editable: false
        }, {
          content: '已完成右滑动变未完成',
          checked: false,
          editable: false
        }, {
          content: '左滑删除',
          checked: false,
          editable: false
        }, {
          content: '点击文字编辑，已完成不可编辑',
          checked: false,
          editable: false
        }
      ]
    }
  },
  computed: {
    isInEdit: function () {
      return this.todoList.some(item => {
        return item.editable
      })
    }
  },
  created() {

  },
  methods: {
    setLocalstorage() {
      localStorage.setItem('datas', JSON.stringify(this.todoList))
    },
    addToContent() {
      $('.weui-cell.weui-cell_vcode').removeClass('invalid')
      if ($('#newContent').val()) {
        this.todoList.unshift({
          content: $('#newContent').val(),
          checked: false,
          editable: false
        })
        $('#newContent').val('')
      } else {
        $('.weui-cell.weui-cell_vcode').addClass('invalid')
      }
      $('#newContent').focus()
      this.setLocalstorage()
    },
    clickToEdit(e) {
      var $parent = $(e.target).parents('.todo-menu-item')
      if ($parent.hasClass('done')) return
      this.clickToSee()
      var index = $parent.index()
      this.todoList[index].editable = true
      $(e.target).siblings('input').show().focus()
      var margin = 60 - Number($(e.target).offset().top)
      $parent.css({ 'margin-top': margin + 'px' })
      $parent.next().css({ 'margin-top': -margin + 'px' })
      e.stopPropagation()
    },
    clickToSee() {
      if (this.todoList.some(item => {
        return !item.content
      })) return

      this.todoList.forEach(item => {
        item.editable = false
      })
      $('.todo-menu-item').css({ 'margin-top': 0 })
      this.setLocalstorage()
    },
    clickToStop(e) {
      e.stopPropagation()
    },
    findTarget(e) {
      var $this
      if ($(e.target).hasClass('item-content')) {
        $this = $(e.target)
      } else {
        $this = $(e.target).parents('.item-content')
      }
      return $this
    },
    touchstart(e) {
      var $this = this.findTarget(e)
      var $parent = $this.parent()
      $this.data('currentLocation', e.targetTouches[0].pageX)
      $parent.addClass('clicked')
    },
    touchmove(e) {
      var $this = this.findTarget(e)
      var $parent = $this.parent()
      var originPageX = Number($this.data('currentLocation'))
      var currentPageX = e.targetTouches[0].pageX
      var translateX = currentPageX - originPageX
      if (translateX < -80) translateX = (-80 + translateX) / 2
      if (translateX > 80) translateX = (80 + translateX) / 2
      $this[0].parentNode.style.WebkitTransform = "translateX(" + translateX + "px)"
      $this.data('movingTranslateX', translateX)
    },
    touchend(e) {
      var $this = this.findTarget(e)
      var $parent = $this.parent()
      $parent.removeClass('clicked')
      var tlx = Number($this.data('movingTranslateX'))
      if (tlx <= -80) {
        $this.data('movingTranslateX', 0)
        var index = $parent.index()
        $parent.addClass('removing')
        this.todoList.splice(index, 1)
        setTimeout(() => {
          $parent.removeClass('removing')
          this.setLocalstorage()
          console.log('remove')
        }, 1000)
      } else if (tlx >= 80) {
        $this.data('movingTranslateX', 0)
        var index = $parent.index()
        if (this.todoList[index].content) {
          this.todoList[index].checked = !this.todoList[index].checked
          setTimeout(() => {
            if (this.todoList[index].checked) {
              this.todoList.push(this.todoList.splice(index, 1)[0])
            } else {
              this.todoList.unshift(this.todoList.splice(index, 1)[0])
            }
            this.setLocalstorage()
          }, 500)
        }
      }
      $this[0].parentNode.style.WebkitTransform = "translateX(0)"
    }
  }
}
</script>


